import { HtsReportService } from "@/apps/HTS/services/hts_report_service";
import { Service } from "@/services/service";
import { toastWarning } from "@/utils/Alerts";
import { appendCsvSignature, toCsv, toPDFfromHTML, toTablePDF } from "@/utils/Export";
import { DrillPatients } from "@/utils/PatientDrilldown";
import { DateSelection } from "@/utils/PromptDateSelection";
import { loadingController } from "@ionic/vue";
import dayjs from "dayjs";
import { isEmpty } from "lodash";
import { computed, onMounted, reactive } from "vue";
import { MohTemplateIndicatorInterface, MohTemplateParamInterface } from "./interface";

const data = reactive({
    model: [] as MohTemplateIndicatorInterface[],
    indicators: {} as any,
    dateGenerated: '',
    startDate: '',
    endDate: '',
    title: '',
    period: ''
})

const indicators = computed(() => {
    return data.model.map((indicator: MohTemplateIndicatorInterface) => {
        if (typeof indicator.value === 'function') {
            const value = indicator.value(data.indicators)
            if (Array.isArray(value)) {
                indicator._value = `${value.length}`
                indicator._drill = value as any
            } else {
                indicator._value = value
            }
        }
        return indicator
    })
})

const exportable = computed(() => {
    const [_, ...rows] = indicators.value.map((indicator: any) => {
        return [ 
            indicator.index || '', 
            indicator.label, 
            !indicator.index ? '' : indicator._value ||  0
        ]
    })
    return {
        rows,
        columns: [['#',indicators.value[0].label, '#']]
    }
})

export function MohController(params: MohTemplateParamInterface) {
    if (params.title != data.title) data.indicators = {}
    data.title = params.title
    data.model = params.indicators
    const generate = async (startDate='', endDate='') =>  {
        (await loadingController.create({ message: "Please wait .."})).present();
        try {
            const report = new HtsReportService(params.name)
            report.startDate = startDate || data.startDate
            report.endDate = endDate || data.endDate
            data.indicators = await report.requestReport({ type: 'moh' })
            data.dateGenerated = dayjs(Service.getSessionDate()).format('DD/MMM/YYYY HH:mm:ss')
        } catch (e) {
            toastWarning('Unable to generate report')
        }
        loadingController.dismiss()
    }

    const configure = () => DateSelection({
        title: data.title,
        onFinish: (startDate: string, endDate: string, period: string) => {
            data.period = period
            data.startDate = startDate
            data.endDate = endDate
            generate()
        }
    })

    const drill = (indicator: MohTemplateIndicatorInterface) => {
        if (Array.isArray(indicator._drill)) {
            DrillPatients(indicator.label, indicator._drill)
        }
    }

    const exportPdf = () => {
        const content = document.getElementById("report-content")
        if (content) {
            toPDFfromHTML(`
                <html>
                <head>
                    <link rel="stylesheet" media="print"  href="/assets/css/hts-report-style-print.css"/>
                    <style> 
                        #moh_template { border: solid 1px #3333; width: 100%; } td { font-size: var(--his-sm-font-size); padding: 0.3em; } .main-title { background: #8EAADB; font-weight: bold; border: solid 3px black; } .table-value { padding: 1em; text-align: center; color: #337ab7; font-weight: bold; font-size: var(--his-md-font-size); border: solid 2px black; } .table-section { font-weight: bold; background: #D9E2F3; border: solid 3px black; }
                    </style>
                </head>
                <body>
                    ${content.innerHTML}
                </body>
                </html>
            `, 50)
        }
    }

    const exportCsv = () => {
        const { rows, columns } = exportable.value
        toCsv(columns,
            appendCsvSignature(rows, data), 
            `MOH-${data.title}-${Service.getLocationName()}-${data.period}`
       )
    }

    onMounted(() => isEmpty(data.indicators) && configure())

    return {
        drill,
        data,
        indicators,
        exportable,
        generate,
        exportPdf,
        exportCsv,
        configure
    }
}