import { modalController } from "@ionic/vue";
import Drilldown from "@/components/DrillPatientIds.vue"

export async function DrillPatients(title: string, patientIdentifiers: number[]) {
    (await modalController.create({
        component: Drilldown,
        cssClass: 'large-modal',
        componentProps: {
            title,
            patientIdentifiers,
            onFinish: () => modalController.dismiss()
        }
    })).present()
}