
import {
  IonPage,
  IonHeader,
  IonContent,
  IonToolbar,
  IonChip,
  IonFooter,
  IonThumbnail,
  IonImg,
  IonLabel,
  IonItem,
} from "@ionic/vue"
import img from "@/utils/Img";
import HisFooter from "@/components/HisDynamicNavFooter.vue";
import { computed, defineComponent } from "vue";
import { useRouter } from "vue-router";
import { Service } from "@/services/service";

export default defineComponent({
    components: {
        IonLabel,
        IonItem,
        HisFooter, 
        IonPage, 
        IonHeader,
        IonContent, 
        IonToolbar, 
        IonChip, 
        IonFooter,
        IonThumbnail,
        IonImg
    },
    props: {
        title: {
            type: String,
        },
        period: {
            type: String
        },
        logo: {
            type: String
        },
        toCSV: {
            type: Function,
            required: false
        },
        toPDF: {
            type: Function,
            required: false
        },
        configure: {
            type: Function,
            required: false
        },
        refreshReport: {
            type: Function,
            required: true
        },
        dateGenerated: {
            type: String,
            default: '-'
        }
    },
    setup(props) {
        const router = useRouter()
        const buttons: any = []
        const appVersion: string = Service.getCoreVersion()
        const apiVersion: string = Service.getApiVersion()
        const reportLogo = computed(() => props.logo ? img(props.logo) : null)

        function btn(params: any, onClick: Function) {
            return {
                name: params.name || "Btn",
                size: params.size || "large",
                slot: params.slot || "start",
                color: params.color || "primary",
                visible: params.visible || true,
                onClick
            }
        }
        if (typeof props.toCSV === 'function') {
            buttons.push(btn({ name: 'CSV' }, props.toCSV))
        }
        if (typeof props.toPDF === 'function') {
            buttons.push(btn({ name: 'PDF'}, props.toPDF))
        }
        if (typeof props.configure === 'function') {
            buttons.push(btn({ 
                name: 'Set Date',
                slot: 'end',
                color: 'primary'
            }, () => props.configure && props.configure()))
        }
        buttons.push(btn({
            name: 'Refresh',
            slot: 'end',
            color: 'warning'
        }, () => props.refreshReport()))
        buttons.push(btn({
            name: 'Finish',
            slot: 'end',
            color: 'success'
        }, () => router.push('/')))

        return {
            buttons,
            appVersion,
            apiVersion,
            reportLogo
        }
    }
})
